<template>
  <main class="register">
    <section class="page-title-section theme-bg">
        <div class="container">
            <div class="page-title">
                <h1>Add Account to {{estate.estateName}}</h1>
                <ol class="breadcrumb justify-content-center">
                    <li><router-link to="/">Home</router-link></li>
                    <li class="active">Add Meter</li>
                </ol>
            </div>
        </div>
    </section>

    <section>
      <div class="container container-large">
        <div class="row  sm-p-30px-b">
          <div class="col-md-6 offset-md-3 col-lg-6 offset-lg-3 right-img md-m-30px-t">

            <form class="contactform py-3 my-3" v-on:submit.prevent="onSubmit">
                <div class="row">
                            <div class="col-md-6">
                                <div class="form-group">
                                    <div class="form-group">
                                    <select v-model="user.meterType" id="meterType" class="form-control">   
                                        <option value="">Meter Type</option>
                                        <option value="water">Water</option>
                                        <option value="electricity">Electricity</option>
                                        <option value="gas">Gas</option>
                                    </select>
                                    <div class="invalid-feedback block" v-if="errors.meterType">{{errors.meterType}}</div>
                                </div>
                                </div>
                            </div>
                            <div class="col-md-6">
                                <input id="meterNumber" v-model="user.meterNumber" type="number" min="0" placeholder="Meter Number" class="form-control" required>
                                    <div class="invalid-feedback block" v-if="errors.meterNumber">{{errors.meterNumber}}</div>
                                
                            </div>
                        </div>
                <div class="row">
                    <div class="col-md-6">
                        <div class="form-group">
                            <input id="firstName" v-model="user.firstName" type="text" placeholder="First Name"  class="form-control" required>
                            <div class="invalid-feedback block" v-if="errors.firstName">{{errors.firstName}}</div>
                        </div>
                    </div>
                    <div class="col-md-6">
                        <div class="form-group">
                            <input id="lastName" v-model="user.lastName" type="text" placeholder="Last Name"  class="form-control" required>
                            <div class="invalid-feedback block" v-if="errors.lastName">{{errors.lastName}}</div>
                        </div>
                    </div>
                    <div class="col-md-6">
                        <div class="form-group">
                            <input id="email" v-model="user.email" type="email" placeholder="Email" class="form-control" required>
                            <div class="invalid-feedback block" v-if="errors.email">{{errors.email}}</div>
                        </div>
                    </div>
                    <div class="col-md-6">
                        <div class="form-group">
                            <input id="phoneNumber" v-model="user.phoneNumber" type="text" placeholder="Phone Number" class="form-control" required>
                            <div class="invalid-feedback block" v-if="errors.phoneNumber">{{errors.phoneNumber}}</div>
                        </div>
                    </div>
                      <div class="col-md-6">
                        <div class="form-group">
                            <input id="address1" v-model="user.address1" type="text" placeholder="Address 1" class="form-control" required>
                            
                        </div>
                    </div>
                    <div class="col-md-6">
                        <div class="form-group">
                            <input id="address2" v-model="user.address2" type="text" placeholder="Address 2" class="form-control" >
                  </div>
                    </div>
                     <div class="col-md-6">
                          <p class="mb-0">Set an account password</p>
                          </div>
                    <div class="col-md-6"></div>
                    <div class="col-md-6">
                        <div class="form-group">
                            <input id="password" v-model="user.password" type="password" placeholder="Password" name="password" class="form-control" required>
                            <div class="invalid-feedback block" v-if="errors.password">{{errors.password}}</div>
                        </div>
                    </div>
                    <div class="col-md-6">
                        <div class="form-group">
                            <input id="confirmPassword" v-model="user.confirmPassword" type="password" placeholder="Confirm Password" class="form-control" required>
                            <div class="invalid-feedback block" v-if="errors.confirmPassword">{{errors.confirmPassword}}</div>
                        </div>
                    </div>

                   
                    
                    <div class="col-md-12">
                        
                    </div>

                    <div class="col-md-12">
                         <div class="send">
                            <button class="btn btn-primary" type="submit" name="send"><span class="ti-plus"></span> Add Account</button>
                        </div>
                    
                         <div class="clearfix"></div> <br/>
                        <p class="gray-dark">Cancel and go back to <router-link to="estates"><span class="ti-arrow-left"></span> Estates</router-link> </p>
                   </div>
                </div>
              </form>

            
          </div>
        </div> 
      </div> 
    </section>
    
  </main>
</template>

<script>
import * as auth from '../../services/auth_service';
  export default {
    name: 'addMeter',
    data (){
        return{
            estate:[],
             user: {
                    firstName: '',
                    lastName: '',
                    email: '', 
                    phoneNumber: '',
                    password: '',  
                    confirmPassword: '',                                                               
                    accountType: 1315,    
                    meterNumber: '', 
                    address1:'',
                    address2:'',
                    meterType: '', 
                    
                    estateName: "",
                    estateId:"",
                    registrantId:1,       
                },
                id:"",
                errors: {},
                message: ''
        }
        
    },
    created(){
        //const id = auth.getUserId(req);this.estate = this.$route.params.estate;
    if(!this.$route.params.estate){
        this.$router.push('estates');
        
      }
      else{
        this.estate = this.$route.params.estate;
        this.user.estateName = this.estate.estateName;
        this.user.estateId = this.estate._id;
        return this.estate;
      }
       
        
        //console.log(this.$route);
    },
      methods: {
            onSubmit: async function() {
                await auth.registerUser(this.user)
                .then(response => {
                    response = {};
                    this.errors = {};
                    this.$store.state.commonMessage = 'registration'
                    this.$router.push({ name: 'login' });
                })
                .catch(error => {
                    console.log(error.response.data);
                    if (error.response.data.message) {
                        this.errors = {};
                        this.flashMessage.error({
                            title: 'Error',
                            message: 'Error occured please reload the page and try again.',
                            time: 5000,
                            icon: '/img/user-dummy.52069854.jpg'
                        });
                     } else {
                        this.errors = error.response.data.errors;
                    }
                });
            }
        }
  }
</script>
